import React from "react";
import styled from "styled-components";
import { GithubContext } from "../context/context";
import { ExampleChart, Pie3D, Column3D, Bar3D, Doughnut2D } from "./Charts";
const Repos = () => {
  const { githubRepos } = React.useContext(GithubContext);

  const chartData = [];

  const arrLang = githubRepos.reduce((total, item) => {
    const { language, stargazers_count } = item;
    if (!language) return total;
    if (!total[language]) {
      total[language] = { label: language, value: 1, stars: stargazers_count };
    } else {
      total[language] = {
        ...total[language],
        value: total[language].value + 1,
        stars: total[language].stars + stargazers_count,
      };
    }
    return total;
  }, {});
  const mostLang = Object.values(arrLang)
    .sort((a, b) => {
      return b.value - a.value;
    })
    .slice(0, 5);

  const mostPopularStars = Object.values(arrLang)
    .sort((a, b) => {
      return b.stars - a.stars;
    })
    .slice(0, 5)
    .map((item) => {
      return { ...item, value: item.stars };
    });

  let { stars, forks } = githubRepos.reduce(
    (total, item) => {
      const { stargazers_count, name, forks } = item;
      total.stars[stargazers_count] = { label: name, value: stargazers_count };
      total.forks[forks] = { label: name, value: forks };

      return total;
    },
    { stars: {}, forks: {} }
  );

  stars = Object.values(stars).slice(-5).reverse();
  forks = Object.values(forks).slice(-5).reverse();

  // ****************************** my solution, but I have O^2 difficult algoritm

  // const arrLang = new Set();
  // githubRepos.map((item) => {
  //   const { language } = item;
  //   if (language !== null) {
  //     arrLang.add(item.language);
  //   }
  // });

  // for (let lang of arrLang) {
  //   chartData.push({
  //     label: lang,
  //     value: githubRepos.filter((item) => item.language === lang).length,
  //   });
  // }
  // const repoLang = chartData
  //   .sort((a, b) => {
  //     return b.value - a.value;
  //   })
  //   .slice(0, 5);

  //*********************** */

  return (
    <section className="section">
      <Wrapper className="section-center">
        <Pie3D data={mostLang} />
        <Column3D data={stars}></Column3D>
        <Doughnut2D data={mostPopularStars}></Doughnut2D>
        <Bar3D data={forks} />
      </Wrapper>
    </section>
  );
};

const Wrapper = styled.div`
  display: grid;
  justify-items: center;
  gap: 2rem;
  @media (min-width: 800px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: 1200px) {
    grid-template-columns: 2fr 3fr;
  }

  div {
    width: 100% !important;
  }
  .fusioncharts-container {
    width: 100% !important;
  }
  svg {
    width: 100% !important;
    border-radius: var(--radius) !important;
  }
`;

export default Repos;
